/*================ Typography =================*/

$mdc-typography-font-family: 'Cabin', sans-serif;
$mdc-typography-color: $textSecondary;
$mdc-typography-styles-headline1: ( 
  font-size: 9.6rem,
  line-height: 9.6rem,
  letter-spacing: -.15rem
  );
$mdc-typography-styles-headline2: (
   font-size: 6rem,
   line-height: 6rem,
   letter-spacing: -.05rem
  );
$mdc-typography-styles-headline3: ( 
  font-size: 4.8rem,
  line-height: 4.8rem,
  letter-spacing: normal
  );
$mdc-typography-styles-headline4: ( 
  font-size: 3.4rem,
  line-height: 4rem,
  letter-spacing: .025rem
  );
$mdc-typography-styles-headline5: ( 
  font-size: 2.4rem,
  line-height: 3.2rem,
  letter-spacing: normal
  );
$mdc-typography-styles-headline6: ( 
  font-size: 2.0rem,
  line-height: 3.2rem,
  letter-spacing: .025rem
  );
$mdc-typography-styles-subtitle1: ( 
  font-size: 1.6rem,
  line-height: 2.8rem,
  letter-spacing: .015rem
  );
$mdc-typography-styles-subtitle2: ( 
  font-size: 1.4rem,
  line-height: 2.2rem,
  letter-spacing: .01rem
  );
$mdc-typography-styles-body1: (
   font-size: 1.6rem,
   line-height: 2.4rem,
   letter-spacing: .05rem
  );
$mdc-typography-styles-body2: ( 
  font-size: 1.4rem,
  line-height: 2.0rem,
  letter-spacing: .025rem
  );
$mdc-typography-styles-button: ( 
  font-size: 1.4rem,
  line-height: 3.6rem,
  letter-spacing: .125rem
  );
$mdc-typography-styles-overline: ( 
  font-size: 1.2rem,
  line-height: 3.2rem,
  letter-spacing: .2rem
  );
$mdc-typography-styles-caption: ( 
  font-size: 1.2rem,
  line-height: 2rem,
  letter-spacing: .4rem
  );

.add-to-cart-text {
    font-family: "Cabin", sans-serif;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-size: 1.4rem;
    line-height: 3.6rem;
    font-weight: 500;
    letter-spacing: 0.125em;
    text-decoration: none;
    text-transform: capitalize;
}



body, a {
  font-family:'Cabin', sans-serif;
  font-weight: 500;
  color: $textSecondary;
  font-variant-numeric: lining-nums tabular-nums;   
  padding: 0;
  letter-spacing: .05em;
  text-decoration: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


h1,
.h1 {
  margin: 16px 0px 0px 0px;
  font-size: 11.2rem;
  font-weight: 400;
  text-transform: uppercase;
}

h2,
.h2 {
  margin: 16px 0px 0px 0px;
  font-size: 7rem;
  font-weight: 400;
  text-transform: uppercase;
}

h3,
.h3 {
  margin: 16px 0px 0px 0px;
  font-size: 5.6rem;
  font-weight: 400;
  text-transform: uppercase;
}

h4,
.h4 {
  margin: 16px 0px 0px 0px;
  font-size: 4rem;
  font-weight: 400;
  letter-spacing: .025em;
  text-transform: uppercase;
}

h5,
.h5 {
  margin: 16px 0px 0px 0px;
  font-size: 2.8rem;
  font-weight: 400;
  text-transform: uppercase;
}

h6,
.h6 {
  margin: 16px 0px 0px 0px;
  font-size: 2.3rem;
  font-weight: 400;
  letter-spacing: .025em;
  text-transform: uppercase;
}

body,
.body {
  margin: 16px 0px 0px 0px;
  font-size: 1.846rem;
  font-weight: 400;
  letter-spacing: .05em;
}

.body2 {
  margin: 16px 0px 0px 0px;
  font-size: 1.615rem;
  font-weight: 400;
  letter-spacing: .025em;
}

.buttontext {
  
  font-size: 1.604rem;
  font-weight: 600;
  letter-spacing: .125em;
  text-transform: uppercase;
}

.caption {
  margin: 16px 0px 0px 0px;
  font-size: 1.4rem;
  font-weight: 400;
  letter-spacing: .04em;
}

.overline {
  margin: 16px 0px 0px 0px;
  font-size: 1.4rem;
  font-weight: 400;
  letter-spacing: .2em;
  text-transform: uppercase;
}

.subtitle1 {
  margin: 16px 0px 0px 0px;
  font-size: 1.834rem;
  font-weight: 500;
  letter-spacing: .015em;
  text-transform: uppercase;
}

.subtitle2 {
  margin: 16px 0px 0px 0px;
  font-size: 1.6rem;
  font-weight: 500;
  letter-spacing: .01em;
  text-transform: uppercase;
}

.number-text {
  margin: 16px 0px 0px 0px;
  font-family: 'Cabin';
}

nav.breadcrumb a, nav.breadcrumb span {
    text-transform: uppercase;
    font-weight: 700;
    color: $surfaceColor600;
}

input#Quantity-featured-product {
    
    text-align: center;
    width: 3rem;
    padding: .1rem .1rem .1rem .9rem;
    margin: 0 0 0 1rem;
}

h1.featured-product-title.h5 {
  margin: 0;
}

.nav{
	font-size: 1rem;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	padding: 0;
}

.h1top {
  margin: 0;
}

.svg .st0 {
  fill: $color-body-text;
}

.subtitle3 {
    font-size: 1.2rem;
    text-transform: uppercase;
    color: #757575;
    font-weight: 600;
}


